.imgWrap {
  overflow: hidden;
  height: 100%;
  margin-bottom: 16px;
}

.imgMovie {
  display: block;
  width: 100%;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.imgWrap:hover .imgMovie {
  transform: scale(1.1);
}
.nameMovie {
  margin-bottom: 16px;
  font-size: 20px;
}
